/* Home */

.home-darkmode {
  background-color: black;
  color: white;
}

/* Header */

.header {
  display: flex;
  padding-left: 60px;
  padding-right: 80px;
  height: 80px;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 2;
}

.header-darkmode {
  color: white;
}

.header:hover {
  background-color: rgba(247, 247, 244, 1);
}

.header-spring {
  position: sticky;
  background-color: rgba(255, 192, 203, 0.8);
}
.header-summer {
  position: sticky;
  background-color: rgba(135, 207, 235, 0.8);
}
.header-autumn {
  position: sticky;
  background-color: rgba(255, 69, 0, 0.8);
}
.header-winter {
  position: sticky;
  background-color: rgba(0, 255, 255, 0.5);
}

.darkmode .header {
  background-color: rgba(75, 74, 74, 0.8);
}

.header-spring:hover {
  background-color: pink;
}
.header-summer:hover {
  background-color: skyblue;
}
.header-autumn:hover {
  background-color: rgba(255, 69, 0, 1);
}
.header-winter:hover {
  background-color: aqua;
}

.header-darkmode:hover {
  background-color: rgba(75, 74, 74, 1);
}

.darkmode .header:hover {
  background-color: rgba(75, 74, 74, 1);
}

.header-logo {
  width: 200px;
  display: flex;
  align-items: center;
  font-size: 30px;
  font-weight: bold;
  white-space: nowrap;
}

.header-logo a {
  text-decoration: none;
  color: black;
}

.header-darkmode .header-logo a {
  color: white;
}

.header-nav {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  list-style: none;
  align-items: center;
}

.header-nav-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header-nav-link {
  padding: 0 3vw;
  text-decoration: none;
  color: black;
  font-size: 18px;
  font-weight: bold;
  z-index: 2;
}

.header-darkmode .header-nav a {
  color: white;
}

.header-nav li:hover {
  transform: scale(1.2);
}

.header-button {
  display: flex;
  align-items: center;
}

.header-button button {
  cursor: pointer;
  background: none;
  z-index: 2;
}

.header-button button:hover {
  transform: scale(1.2);
}

.header-darkmode .header-button button {
  color: white;
  border-color: white;
}

.header-nav-submenu {
  display: none;
  position: fixed;
  list-style: none;
  padding-top: 50px;
  padding-left: 20px;
  padding-right: 20px;
  background-color: rgba(247, 247, 244, 1);
  z-index: 1;
  border-radius: 5px;
}

.header-nav-submenu li {
  margin-bottom: 15px;
  padding-bottom: 5px;
  border-bottom: rgba(0, 0, 0, 0.2) solid 1px;
  cursor: pointer;
  white-space: nowrap;
}

.header-nav-menu:hover > .header-nav-submenu {
  display: block;
}

.header-spring .header-nav-submenu {
  background-color: pink;
}
.header-summer .header-nav-submenu {
  background-color: skyblue;
}
.header-autumn .header-nav-submenu {
  background-color: rgba(255, 69, 0, 1);
}
.header-winter .header-nav-submenu {
  background-color: aqua;
}

.header-darkmode .header-nav-submenu {
  background-color: rgba(75, 74, 74, 1);
}

.header-nav-submenu a {
  text-decoration: none;
  color: black;
}

/* Hamburger */

.hamburger {
  display: none;
  padding-left: 10px;
  padding-right: 10px;
  height: 80px;
  background-color: transparent;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  justify-content: space-between;
  align-items: center;
}

.hamburger-darkmode {
  color: white;
}

.hamburger-menu {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.spring .hamburger {
  position: sticky;
  background-color: rgba(255, 192, 203, 0.8);
}
.summer .hamburger {
  position: sticky;
  background-color: rgba(135, 207, 235, 0.8);
}
.autumn .hamburger {
  position: sticky;
  background-color: rgba(255, 69, 0, 0.8);
}
.winter .hamburger {
  position: sticky;
  background-color: rgba(0, 255, 255, 0.8);
}

.spring .hamburger:hover {
  background-color: pink;
}
.summer .hamburger:hover {
  background-color: rgba(135, 207, 235, 1);
}
.autumn .hamburger:hover {
  background-color: rgba(255, 69, 0, 1);
}
.winter .hamburger:hover {
  background-color: rgba(0, 255, 255, 1);
}

.darkmode .hamburger {
  background-color: rgba(75, 74, 74, 0.8);
}

.darkmode .hamburger:hover {
  background-color: rgba(75, 74, 74, 1);
}

.hamburger:hover {
  background-color: rgba(247, 247, 244, 0.8);
}

.hamburger-sidebar {
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  width: 500px;
  background-color: rgba(247, 247, 244, 1);
  overflow: auto;
  z-index: 3;
  display: none;
}

.spring .hamburger-sidebar {
  background-color: pink;
}
.summer .hamburger-sidebar {
  background-color: skyblue;
}
.autumn .hamburger-sidebar {
  background-color: rgba(255, 69, 0, 1);
}
.winter .hamburger-sidebar {
  background-color: aqua;
}

.darkmode .hamburger-sidebar {
  background-color: rgba(75, 74, 74, 1);
}

.hamburger-nav {
  list-style: none;
}

.hamburger-nav-button {
  margin-top: 20px;
  margin-bottom: 20px;
  cursor: pointer;
}

.hamburger-nav-menu {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.hamburger-nav-link {
  display: block;
  text-decoration: none;
  color: black;
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 10px;
}

.hamburger-nav-submenu {
  list-style: none;
  padding-left: 0;
  padding-bottom: 10px;
}

.hamburger-nav-submenu li {
  padding-bottom: 10px;
  cursor: pointer;
}

.hamburger-nav-submenu li:hover {
  font-weight: bold;
}

