/* Activity */

.activity {
  padding-left: 30px;
  padding-right: 30px;
}

.activity-content {
  width: 100%;
  height: 70vh;
  min-width: 500px;
  min-height: 500px;
  margin-bottom: 100px;
}

.activity-content-up {
  display: grid;
  grid-template-columns: 1fr 1fr;
  height: 49.5%;
  gap: 10px;
  margin-bottom: 1%;
}

.activity-content-1 {
  border-radius: 5px;
  color: white;
  font-size: 30px;
  font-weight: bold;
  cursor: default;
  overflow: hidden;
}

.activity-content-1-image {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;

  background-size: cover;
  background-position: center;

  transition: transform 0.5s ease;
}

.activity-content-1-image:hover {
  transform: scale(1.1);
}

