/* Tip */

.tip {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  border-top: 2px solid rgba(255, 192, 203, 0.6);
  margin: 20px;
}
.summer .tip {
  border-top: 2px solid rgba(115, 196, 228, 0.6);
}
.autumn .tip {
  border-top: 2px solid rgba(255, 69, 0, 0.6);
}

.winter .tip {
  border-top: 2px solid rgba(0, 255, 255, 0.6);
}

.tip-content {
  padding-top: 20px;
  padding-bottom: 20px;
  margin-right: 20px;
  display: flex;
  border-bottom: 2px solid rgba(255, 192, 203, 0.6);
}
.summer .tip-content {
  border-bottom: 2px solid rgba(115, 196, 228, 0.6);
}
.autumn .tip-content {
  border-bottom: 2px solid rgba(255, 69, 0, 0.6);
}

.winter .tip-content {
  border-bottom: 2px solid rgba(0, 255, 255, 0.6);
}

.tip-content img {
  width: 30%;
  margin-right: 5%;
  border-radius: 5px;
  object-fit: cover;
  min-height: 200px;
}

.tip-content-text {
  width: 60%;
}

.tip-content h4 {
  margin-top: 0;
}

.tip-content a {
  margin-top: 20px;
  color: #551a8b;
  font-weight: bold;
  font-size: 15px;
  text-decoration: none;
  display: block;
}

.darkmode .tip-content a {
  color: rgb(137, 155, 241);
}

/* Gallery */

.gallery {
  max-width: 90vw;
}

.gallery-item {
  width: 100%;
  margin-bottom: 100px;
}

.gallery-item-wrapper {
  display: flex;
  height: 25vh;
  align-items: center;
}

.gallery-items-visible {
  flex-grow: 1;
  overflow: hidden;
  height: 100%;
  display: flex;
}

.gallery-items {
  display: flex;
  width: 10000px;
  height: 100%;
  transition: margin 0.5s ease;
}

.gallery-item-content {
  width: 250px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-right: 10px;
}

.gallery-item-content img {
  height: 80%;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 5px;
}

.gallery-description {
  width: 100%;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-weight: bold;
}

.gallery-item-left-button {
  border: none;
  font-weight: bold;
  font-size: 20px;
  background-color: rgba(255, 192, 203, 0.5);
  border-radius: 50%;

  pointer-events: none;
  opacity: 0.5;
}
.summer .gallery-item-left-button {
  background-color: rgba(115, 196, 228, 0.5);
}
.autumn .gallery-item-left-button {
  background-color: rgba(255, 69, 0, 0.5);
}
.winter .gallery-item-left-button {
  background-color: rgba(0, 255, 255, 0.6);
}

.gallery-item-left-button:hover {
  transform: scale(1.5);
}

.gallery-item-right-button {
  border: none;
  font-weight: bold;
  font-size: 20px;
  background-color: rgba(255, 192, 203, 0.5);
  border-radius: 50%;
}
.summer .gallery-item-right-button {
  background-color: rgba(115, 196, 228, 0.5);
}
.autumn .gallery-item-right-button {
  background-color: rgba(255, 69, 0, 0.5);
}
.winter .gallery-item-right-button {
  background-color: rgba(0, 255, 255, 0.6);
}

.gallery-item-right-button:hover {
  transform: scale(1.5);
}

/* Board */

.board {
  margin-left: 5vw;
  margin-right: 5vw;
}

.board-search {
  margin-bottom: 20px;
}

.board-inputwrapper {
  position: relative;
  width: fit-content;
}

.board input {
  outline: none;
}

.board-input-resetbutton {
  background: none;
  border: none;
  cursor: pointer;
  position: absolute;
  top: 5px;
  right: 0;
}

.board-item {
  margin-bottom: 20px;
}

.board-item-post {
  padding: 10px;
  display: flex;
  background-color: rgba(255, 192, 203, 0.3);
  cursor: pointer;
  border-radius: 5px;
}
.summer .board-item-post {
  background-color: rgba(115, 196, 228, 0.3);
}
.autumn .board-item-post {
  background-color: rgba(255, 69, 0, 0.3);
}
.winter .board-item-post {
  background-color: rgba(0, 255, 255, 0.6);
}

.board-item-post-postnumber {
  margin-right: 20px;
  border-right: 2px solid pink;
  padding-right: 15px;
  white-space: nowrap;
}
.summer .board-item-post-postnumber {
  border-right: 2px solid skyblue;
}
.autumn .board-item-post-postnumber {
  border-right: 2px solid rgb(245, 123, 79);
}
.winter .board-item-post-postnumber {
  border-right: 2px solid aqua;
}

.darkmode .board-item-post-postnumber {
  border-right: 2px solid black;
}

.board-item-post-title {
  flex-grow: 1;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-right: 20px;
}

.board-item-post-user {
  border-left: 2px solid pink;
  padding-left: 15px;
  white-space: nowrap;
}
.summer .board-item-post-user {
  border-left: 2px solid skyblue;
}
.autumn .board-item-post-user {
  border-left: 2px solid rgb(245, 123, 79);
}
.winter .board-item-post-user {
  border-left: 2px solid aqua;
}

.darkmode .board-item-post-user {
  border-left: 2px solid black;
}

.board-item-content {
  background-color: rgba(255, 192, 203, 0.4);
  padding: 20px;
  display: none;
  cursor: pointer;
  border-radius: 5px;
}
.summer .board-item-content {
  background-color: rgba(115, 196, 228, 0.4);
}
.autumn .board-item-content {
  background-color: rgba(255, 69, 0, 0.4);
}
.winter .board-item-content {
  background-color: rgba(0, 255, 255, 0.4);
}

.board-item-content-display {
  display: block;
}

/* 반응형 */

@media (max-width: 1000px) {
  .hamburger {
    display: flex;
  }
  .header {
    display: none;
  }

  .hamburger-sidebar {
    display: block;
  }
  .maincontent {
    flex-direction: column-reverse;
    height: auto;
    margin-bottom: 50px;
  }
  .maincontent1-left {
    width: 100%;
    height: auto;
  }
  .maincontent1-left-article {
    padding-right: 5vw;
    padding-left: 5vw;
  }
  .maincontent1-left-button {
    margin-left: 5vw;
  }
  .maincontent1-right {
    width: 100%;
    height: 100vw;
  }
  .maincontent1-right img {
    margin-right: 5vw;
    margin-left: 5vw;
  }
  .maincontent1-right-button {
    padding-right: 0;
  }
  .maincontent2 {
    flex-direction: column;
    height: auto;
    margin-bottom: 50px;
  }
  .maincontent2-left {
    width: 100%;
    height: 100vw;
  }
  .maincontent2-left img {
    margin-right: 5vw;
    margin-left: 5vw;
  }
  .maincontent2-left-button {
    padding-left: 0;
  }
  .maincontent2-right {
    width: 100%;
    height: auto;
  }
  .maincontent2-right-article {
    padding-right: 5vw;
    padding-left: 5vw;
  }
  .maincontent2-right-button {
    margin-left: 5vw;
  }
  .carousel-button {
    gap: 5vh;
    width: 20vw;
  }
  .carousel-button button {
    font-size: 20px;
  }
  .carousel-button-autoslide {
    font-size: 16px;
  }
}

@media (max-width: 700px) {
  .hamburger-sidebar {
    width: 100%;
  }

  .subpage-maincontent {
    margin-left: 0;
    margin-right: 0;
  }
  .intro {
    margin-left: 5vw;
    margin-right: 5vw;
  }
  .activity {
    margin-left: 5vw;
    margin-right: 5vw;
  }
  .tip {
    margin-left: auto;
    margin-right: auto;
    display: block;
    margin-left: 5vw;
    margin-right: 5vw;
  }
  .board {
    width: 80vw;
    margin-left: auto;
    margin-right: auto;
  }

  .activity {
    padding: 0;
  }
  .activity-content {
    min-width: 0;
    min-height: 0;
    height: auto;
  }
  .activity-content-up {
    display: block;
    height: auto;
  }
  .activity-content-1 {
    height: 80vw;
    margin-bottom: 30px;
  }
  .activity-content-up-right {
    height: 80vw;
    margin-bottom: 30px;
  }
  .activity-content-down {
    display: block;
    height: auto;
  }
  .activity-content-down-left {
    height: 80vw;
    margin-bottom: 30px;
  }
  .activity-content-10 {
    height: 80vw;
    margin-bottom: 30px;
  }
  .tip-content {
    flex-direction: column;
    align-items: center;
    margin-right: 0;
  }
  .tip-content img {
    width: 80vw;
    height: 80vw;
    margin-bottom: 20px;
    margin-top: 10px;
    min-height: 0;
    margin-right: 0;
  }
  .tip-content-text {
    width: 80vw;
    margin-bottom: 20px;
  }
  .gallery-item h2 {
    margin-left: 5vw;
  }
  .gallery-item-content {
    width: 70vw;
    max-width: 250px;
  }
  .board-item-post {
    font-size: 15px;
    padding: 5%;
  }
  .board-item-post-postnumber {
    margin-right: 2vw;
    padding-right: 2vw;
  }
  .board-item-post-title {
    margin-right: 2vw;
  }
  .board-item-post-user {
    padding-left: 2vw;
  }
}
